.brick-box-moz {
  display: -ms-grid;
  display: grid;
  width: 100%;
  height: 100%;
  opacity: 0;
  background: #272822;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  color: white;
  font-weight: 900;
  font-size: 1.5rem;
  -webkit-transition: -webkit-transform calc(var(--box-trans-time) * 1.5s)
    ease-in;
  transition: -webkit-transform calc(var(--box-trans-time) * 1.5s) ease-in;
  -o-transition: transform calc(var(--box-trans-time) * 1.5s) ease-in;
  transition: transform calc(var(--box-trans-time) * 1.5s) ease-in;
  transition: transform calc(var(--box-trans-time) * 1.5s) ease-in,
    -webkit-transform calc(var(--box-trans-time) * 1.5s) ease-in;
}

.brick-box-moz p {
  -ms-grid-column-align: center;
  justify-self: center;
  -ms-flex-item-align: center;
  -ms-grid-row-align: center;
  align-self: center;
}

.start-title {
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  text-align: center;
}

.boxAnimMoz {
  -webkit-animation: animZ calc(var(--box-anim-time) * 0.75s) ease-in forwards;
  animation: animZ calc(var(--box-anim-time) * 0.75s) ease-in forwards;
}

.boxAnimRev {
  -webkit-animation: animZRev calc((var(--box-anim-time) / 2) * 0.75s) ease-in
    forwards;
  animation: animZRev calc((var(--box-anim-time) / 2) * 0.75s) ease-in forwards;
}

@-webkit-keyframes animZ {
  0% {
    opacity: 1;
    -webkit-transform: translateZ(2000px);
    transform: translateZ(2000px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateZ(0px);
    transform: translateZ(0px);
  }
}

@keyframes animZ {
  0% {
    opacity: 1;
    -webkit-transform: translateZ(2000px);
    transform: translateZ(2000px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateZ(0px);
    transform: translateZ(0px);
  }
}

@-webkit-keyframes animZRev {
  0% {
    opacity: 1;
    -webkit-transform: translateZ(0px);
    transform: translateZ(0px);
  }
  80% {
    opacity: 1;
    -webkit-transform: translateZ(1600px);
    transform: translateZ(1600px);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateZ(2000px);
    transform: translateZ(2000px);
  }
}

@keyframes animZRev {
  0% {
    opacity: 1;
    -webkit-transform: translateZ(0px);
    transform: translateZ(0px);
  }
  80% {
    opacity: 1;
    -webkit-transform: translateZ(1600px);
    transform: translateZ(1600px);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateZ(2000px);
    transform: translateZ(2000px);
  }
}

